.header { 
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: sticky;
    top: 0;
    background-color: white;
    z-index: 100;
}

.header_logo { 
    height: 25px;
    object-fit: contain;
    margin-left: 20px;
}

.header_input { 
    display: flex;
    align-items: center;
    width: 40%;
    border: 1px solid black;
}

.header_input > input { 
    flex: 1;
    border: none;
    outline-width: 0;
    
}

.header_inputButton { 
    width: 50px !important;
    background-color:  #f6f6f6;
    border-left: 1px solid lightgray;
    color: gray;
    
}

.header_board { 
    margin-right: 5px;
    
}

.header_icons { 
    display: flex;
    align-items: center;
    
}

.header_icon{ 
    margin-right: 10px;
    
}