.sidebarRow { 
    display: flex;
    align-items: center;
    padding: 10px 20px;
}

.sidebarRow:hover { 
    background-color: lightgray;
    cursor: pointer;
}

.sidebarRow_icon { 
    color: #606060;
    font-size: 25px !important;
}

.sidebarRow_title { 
    flex: 1;
    margin-left: 20px;
    font-size: 14px;
    font-weight: 500;
}

.sidebarRow:hover > .sidebarRow_icon { 
    color: red;
}

.sidebarRow:hover > .sidebarRow_title { 
    font-weight: bolder;
}

.sidebarRow.selected { 
    background-color: lightgray;
    
}

.sidebarRow.selected > .sidebarRow_icon { 
    color: red;
    
}

.sidebarRow.selected > .sidebarRow_title{ 
    font-weight: bold;
    
}